body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #212121;
  background-color: #ffffff;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.sticky-footer {
  flex-grow: 1;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: inline-block;
  padding: 20px 40px;
  box-sizing: border-box;
  border: none;
  background: #ff5900;
  backdrop-filter: blur(2.5px);
  border-radius: 100px;
  text-decoration: none;
  text-align: center;
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 22px;
}
.button:hover {
  background-color: #ca4600;
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--transparent {
  border: 1px solid #ff5900;
  background-color: transparent;
  color: #ff5900;
  font-size: 16px;
  line-height: 20px;
}
.button--grey {
  background-color: rgba(255, 255, 255, 0.2);
  border: 1px solid #ffffff;
  backdrop-filter: blur(2.5px);
}
.button--grey:hover {
  color: #ca4600;
  background-color: rgba(255, 255, 255, 0.5);
}
.button--grey:active {
  background-color: rgba(255, 255, 255, 0.7);
}
.button--callback {
  width: 222px;
  height: 52px;
  padding: 16px 28px;
  display: flex;
  justify-content: space-around;
}
.button--callback:hover .button--callback-phone {
  stroke: #ffffff;
}
.button--callback-phone {
  width: 20px;
  height: 20px;
  stroke: #ff5900;
}
.button--disabled {
  background-color: #e5e5e5;
  color: #ffffff;
  cursor: default;
}
.button--disabled .button--callback-phone {
  stroke: #ffffff;
}
.button--disabled:hover {
  background-color: #e5e5e5;
}
.input {
  border: none;
  border-bottom: 1px solid rgba(51, 51, 51, 0.5);
  background-color: transparent;
}
.input::placeholder {
  color: #333333;
  font-size: 16px;
  line-height: 16px;
}
.main-header {
  width: 1200px;
  margin: 0 auto;
